import React from 'react';
import { default as MuiAvatar, AvatarProps } from '@mui/material/Avatar';

const Avatar = React.forwardRef<HTMLElement, AvatarProps>( ( props, ref ) => {
	return (
		<MuiAvatar
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<AvatarProps>;

export default Avatar;
